body,
html {
  background: #fff;
  position: relative;
  overflow-x: hidden;
  font-family: "AA Stetica", sans-serif;
}

section {
  padding-bottom: 80px;
}

.container {
  max-width: 1452px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: content-box;
}

a {
  color: #575352;
  transition: all 0.3s;
}
a:hover {
  color: #2b7818;
}

.btn__green {
  width: 100%;
  display: block;
  text-align: center;
  padding: 16px 20px;
  font: 500 20px "AA Stetica", sans-serif;
  color: #fff;
  transition: all 0.3s;
  border-radius: 80px;
  background: #5fb528;
  border: none;
}
.btn__green:hover {
  background: #2b7818;
}
.btn__trans {
  width: 100%;
  display: block;
  text-align: center;
  border: 1px solid #5fb528;
  border-radius: 80px;
  padding: 15px 19px;
  font: 500 20px "AA Stetica", sans-serif;
  color: #5fb528;
  transition: all 0.3s;
  background: transparent;
}
.btn__trans:hover {
  background: #f6fbe7;
}

.nav {
  width: 100%;
  display: grid;
  grid-template-columns: 82px 1fr;
  gap: 31px;
  padding: 50px 50px 0;
}
.nav__logo {
  display: block;
}
.nav__logo img {
  width: 100%;
}
.nav__row {
  background: #f7f7f7;
  border-radius: 100px;
  padding: 13px 50px 13px 33px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__row ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 64px;
}
.nav__row ul a {
  font: 500 20px "AA Stetica", sans-serif;
  color: #575352;
}
.nav__row ul a:hover {
  color: #2b7818;
}
.nav__phone {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  margin: 0 40px 0 auto;
}
.nav__phone p {
  font: 500 20px "AA Stetica", sans-serif;
  color: #2b7818;
  white-space: nowrap;
}
.nav__enter {
  font: 500 20px "AA Stetica", sans-serif;
  text-align: center;
  color: #575352;
}
.nav__mob_row, .nav__contact {
  display: none;
}

.header {
  padding: 157px 0;
  position: relative;
  z-index: 2;
}
.header h1 {
  font: 900 44px/1.5 "AA Stetica", sans-serif;
  text-align: center;
  color: #32302f;
}
.header .btn__green {
  margin: 36px auto 0;
  max-width: 460px;
}
.header::before {
  content: "";
  width: 1605px;
  height: 429px;
  border-radius: 100%;
  background: rgba(11, 224, 21, 0.3);
  filter: blur(300px);
  position: absolute;
  top: 88px;
  right: -1242px;
  z-index: -1;
}
.header::after {
  content: "";
  display: block;
  width: 2773px;
  height: 429px;
  border-radius: 100%;
  background: rgba(178, 255, 63, 0.4);
  filter: blur(300px);
  position: absolute;
  top: 88px;
  left: -2415px;
  z-index: -1;
}

.about__tab {
  border-bottom: 1px solid #dedede;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 115px;
}
.about__tab li {
  font: 700 36px "AA Stetica", sans-serif;
  color: #32302f;
  padding: 0 0 32px;
  transition: all 0.3s;
  position: relative;
  cursor: pointer;
}
.about__tab li::before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  bottom: -1px;
  position: absolute;
  left: 0;
  transition: all 0.3s;
  background: #5fb528;
  opacity: 0;
}
.about__tab li.active {
  color: #5fb528;
}
.about__tab li.active::before {
  opacity: 1;
}
.about__tab li:hover {
  color: #5fb528;
}
.about__wrapper {
  margin: 40px 0 0;
  display: none;
}
.about__wrapper.active {
  display: block;
}
.about__center {
  display: grid;
  grid-template-columns: 1fr 436px;
  gap: 60px;
  align-items: center;
}
.about__center img {
  width: 100%;
  border-radius: 12px;
}
.about__center_text {
  display: grid;
  gap: 40px;
}
.about__center_text p {
  font: 400 24px/1.5 "AA Stetica", sans-serif;
  color: #2b7818;
}
.about__center_text h3 {
  font: 500 24px/1.5 "AA Stetica", sans-serif;
  color: #2b7818;
  margin: 0 0 11px;
}
.about__left, .about__right {
  display: grid;
  grid-template-columns: 584px 1fr;
  gap: 77px;
}
.about__left img, .about__right img {
  width: 100%;
  height: 1px;
  min-height: 100%;
  object-fit: cover;
  border-radius: 12px;
}
.about__right {
  grid-template-columns: 1fr 584px;
  margin: 58px 0 0;
}
.about__text h3 {
  margin: 20px 0 0;
  font: 500 24px "AA Stetica", sans-serif;
  color: #32302f;
}
.about__text p {
  margin: 16px 0 0;
  font: 400 20px/1.5 "AA Stetica", sans-serif;
  color: #32302f;
}
.about__text *:nth-child(1) {
  margin: 0;
}
.about__text .btn__green {
  padding: 16px 48px;
  width: max-content;
  margin: 20px 0 0;
}
.about__list h3 {
  font: 500 32px/1.5 "AA Stetica", sans-serif;
  color: #2b7818;
}
.about__list ul {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 27px;
}
.about__list ul li {
  display: grid;
  grid-template-columns: 10px 1fr;
  gap: 20px;
}
.about__list ul li::before {
  content: "";
  display: block;
  width: 10px;
  height: 100%;
  border-radius: 70px;
  background: #c3e49a;
}
.about__list ul p {
  font: 400 20px/1.5 "AA Stetica", sans-serif;
  color: #32302f;
}
.about__block {
  border-radius: 20px;
  background: #f6fbe7;
  margin: 40px 0 0;
  display: grid;
  gap: 35px;
  padding: 40px 30px;
}
.about__block p {
  font: 400 20px/1.5 "AA Stetica", sans-serif;
  color: #2b7818;
}

.title {
  font: 700 36px "AA Stetica", sans-serif;
  color: #32302f;
  padding-bottom: 33px;
  border-bottom: 1px solid #dedede;
}

.result__slider {
  margin: 40px 0 0;
  padding: 0 0 64px;
}
.result__slide {
  display: grid;
  grid-template-columns: 825px 1fr;
  gap: 39px;
}
.result__img {
  display: grid;
  grid-template-columns: 1fr 47px 1fr;
  align-items: center;
  gap: 20px;
}
.result__img img {
  width: 100%;
  height: 100%;
  min-height: 100%;
  object-fit: cover;
  border-radius: 12px;
}
.result__descr {
  padding: 0 0 60px 35px;
  position: relative;
}
.result__descr::before {
  content: "";
  display: block;
  width: 10px;
  height: 64px;
  border-radius: 70px;
  background: #c3e49a;
  position: absolute;
  left: 0;
  top: 0;
}
.result__descr p {
  font: 400 24px/1.5 "AA Stetica", sans-serif;
  color: #32302f;
}
.result .swiper-button-next,
.result .swiper-button-prev {
  position: absolute;
  bottom: 64px;
  left: 900px;
  padding: 0;
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  right: auto;
  top: auto;
  margin: 0;
  transform: none;
}
.result .swiper-button-next::after,
.result .swiper-button-prev::after {
  content: none;
}
.result .swiper-button-next svg,
.result .swiper-button-prev svg {
  width: 25px;
  height: 18px;
}
.result .swiper-button-next {
  left: 974px;
}
.result .swiper-pagination {
  position: absolute;
  bottom: 0;
  left: 0;
  right: auto;
  top: auto;
  transform: none;
  margin: 0;
  width: 825px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}
.result .swiper-pagination span {
  display: block;
  width: 11px;
  height: 11px;
  border-radius: 70px;
  background: #f7f7f7;
  transition: all 0.3s;
  margin: 0 !important;
  opacity: 1 !important;
}
.result .swiper-pagination span.swiper-pagination-bullet-active {
  width: 64px;
  background: #c3e49a;
}

.event .container {
  position: relative;
}
.event__slider {
  margin: 40px 0 0;
}
.event .swiper-button-next,
.event .swiper-button-prev {
  position: absolute;
  bottom: auto;
  left: auto;
  padding: 0;
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  right: 89px;
  top: 0;
  margin: 0;
  transform: none;
}
.event .swiper-button-next::after,
.event .swiper-button-prev::after {
  content: none;
}
.event .swiper-button-next svg,
.event .swiper-button-prev svg {
  width: 25px;
  height: 18px;
}
.event .swiper-button-next {
  right: 15px;
}
.event__slide {
  padding: 25px 30px 23px;
  border-radius: 10px;
  background: #f7f7f7;
  display: grid;
  grid-template-columns: 1fr auto;
  column-gap: 10px;
  align-items: start;
}
.event__slide a {
  margin: 30px 0 0 auto;
  padding: 0;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column: 2/3;
}
.event__slide .btn__green {
  grid-column: 1/3;
  margin: 18px 0 0;
}
.event__desc h3 {
  font: 700 24px "AA Stetica", sans-serif;
  color: #32302f;
}
.event__desc p {
  margin: 18px 0 0;
  font: 400 20px "AA Stetica", sans-serif;
  color: #32302f;
}
.event__date {
  padding: 1px 20px;
  font: 400 20px/1.5 "AA Stetica", sans-serif;
  text-align: center;
  color: #fff;
  border-radius: 70px;
  background: #5fb528;
}
.event__price {
  align-self: end;
  font: 700 24px "AA Stetica", sans-serif;
  color: #32302f;
}

.service h3 {
  margin: 40px 0 0;
  font: 500 32px/1.5 "AA Stetica", sans-serif;
  color: #2b7818;
}
.service__wrapper {
  margin: 33px 0 0;
}
.service__wrapper .swiper-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 36px;
}
.service .swiper-pagination {
  display: none;
}
.service__item {
  padding: 18px 18px 30px;
  border-radius: 20px;
  background: #f6fbe7;
  display: flex;
  flex-direction: column;
  min-height: 502px;
}
.service__item img {
  width: 100%;
  height: 248px;
  border-radius: 20px;
  object-fit: cover;
}
.service__item h4 {
  margin: 24px 0 0;
  font: 700 24px "AA Stetica", sans-serif;
  color: #32302f;
}
.service__item p {
  margin: 18px 0 auto;
  font: 400 20px "AA Stetica", sans-serif;
  color: #32302f;
}
.service__item p.price {
  margin: auto 0 0;
  font: 700 24px "AA Stetica", sans-serif;
}
.service__item .btn__green {
  margin: 20px 0 0;
}
.service__row {
  margin: 20px 0 0;
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 14px;
}
.service__row .btn__green {
  margin: 0;
}
.service__row .btn__trans {
  display: flex;
  justify-content: center;
  align-items: center;
}

.comment__wrapper {
  padding: 0 0 52px;
  display: grid;
  grid-template-columns: calc(100% - 370px) 1fr;
  gap: 35px;
  align-items: center;
  position: relative;
  margin: 40px 0 0;
}
.comment__descr {
  padding: 0 0 0 31px;
  position: relative;
}
.comment__descr::before {
  content: "";
  display: block;
  width: 11px;
  height: 64px;
  border-radius: 70px;
  background: #c3e49a;
  position: absolute;
  left: 0;
  top: 0;
}
.comment__descr p {
  font: 400 24px/1.5 "AA Stetica", sans-serif;
  color: #32302f;
}
.comment__slider {
  width: 100%;
}
.comment__slider img {
  width: 100%;
  border-radius: 20px;
}
.comment .swiper-button-next,
.comment .swiper-button-prev {
  position: relative;
  bottom: auto;
  left: auto;
  padding: 0;
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  right: auto;
  top: auto;
  margin: 0;
  transform: none;
}
.comment .swiper-button-next::after,
.comment .swiper-button-prev::after {
  content: none;
}
.comment .swiper-button-next svg,
.comment .swiper-button-prev svg {
  width: 25px;
  height: 18px;
}
.comment__arrow {
  margin: 24px 0 0;
  display: flex;
  justify-content: start;
  align-items: start;
  gap: 18px;
}
.comment .swiper-pagination {
  position: absolute;
  bottom: 0;
  left: 0;
  right: auto;
  top: auto;
  transform: none;
  margin: 0;
  width: calc(100% - 370px);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}
.comment .swiper-pagination span {
  display: block;
  width: 11px;
  height: 11px;
  border-radius: 70px;
  background: #f7f7f7;
  transition: all 0.3s;
  margin: 0 !important;
  opacity: 1 !important;
}
.comment .swiper-pagination span.swiper-pagination-bullet-active {
  width: 64px;
  background: #c3e49a;
}

.quest__wrapper {
  background: #f7f7f7;
  border-radius: 20px;
  padding: 47px 47px 40px;
  display: grid;
  grid-template-columns: 1fr auto auto;
  gap: 24px;
  align-items: center;
}
.quest__descr h2 {
  font: 700 36px "AA Stetica", sans-serif;
  color: #32302f;
}
.quest__descr p {
  margin: 10px 0 0;
  font: 400 24px/1.5 "AA Stetica", sans-serif;
  color: #32302f;
}
.quest__phone {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 9px;
  font: 700 24px "AA Stetica", sans-serif;
  color: #2b7818;
}
.quest__phone:hover {
  color: #5fb528;
}
.quest__soc {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
}
.quest__soc a {
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
}
.quest__soc a:hover {
  opacity: 0.7;
}

.footer {
  background: #575352;
  padding: 70px 0 0;
}
.footer__wrapper {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 40px;
  align-items: start;
  row-gap: 30px;
}
.footer__logo {
  width: 100%;
  max-width: 330px;
}
.footer__logo img {
  width: 100%;
}
.footer__list {
  grid-row: 1/3;
  grid-column: 2/3;
  border-radius: 70px;
  padding: 18px 70px 15px;
  background: #32302f;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__list a {
  font: 400 20px "AA Stetica", sans-serif;
  color: #fff;
}
.footer__list a:hover {
  color: #c3e49a;
}
.footer__contact {
  grid-row: 1/3;
  grid-column: 3/4;
}
.footer__contact p {
  font: 400 20px "AA Stetica", sans-serif;
  color: #fff;
}
.footer__contact ul {
  display: grid;
  gap: 19px;
  margin: 24px 0 0;
}
.footer__contact ul li {
  display: grid;
  grid-template-columns: 22px 1fr;
  gap: 15px;
}
.footer__soc {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  margin: 24px 0 0;
}
.footer__soc a {
  width: 36px;
  height: 36px;
  border-radius: 100%;
  background: #32302f;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__soc a:hover {
  opacity: 0.7;
}
.footer__info p {
  font: 400 16px/1.5 "AA Stetica", sans-serif;
  color: #fff;
  margin: 0 0 18px;
}
.footer__info a {
  font: 400 16px/1.5 "AA Stetica", sans-serif;
  color: rgba(248, 248, 248, 0.5);
  display: block;
}
.footer__info a:hover {
  color: rgb(248, 248, 248);
}
.footer__descr {
  margin: 42px 0 0;
  position: relative;
  padding: 13px 0;
  z-index: 1;
}
.footer__descr::before {
  content: "";
  display: block;
  width: 100vw;
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
  background: #32302f;
  z-index: -1;
}
.footer__descr p {
  font: 400 16px/1.5625 "AA Stetica", sans-serif;
  color: #575352;
}

@media (max-width: 1600px) {
  .btn__trans, .btn__green {
    font-size: 16px;
    padding: 9px;
  }
  .nav {
    padding: 50px 15px 0;
  }
  .nav__row {
    padding: 13px 20px;
  }
  .nav__row ul {
    gap: 20px;
  }
  .nav__row ul a {
    font-size: 16px;
  }
  .nav__phone {
    gap: 15px;
    margin: 0 20px 0 auto;
  }
  .nav__phone p {
    font-size: 16px;
  }
  .nav__enter {
    font-size: 16px;
  }
  .header .btn__green {
    padding: 16px 20px;
    font-size: 20px;
  }
  .about__left, .about__right {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  .about__tab {
    gap: 50px;
  }
  .about__tab li {
    font-size: 20px;
  }
  .result__slide {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  .result .swiper-pagination {
    width: calc(50% - 10px);
  }
  .result .swiper-button-prev {
    left: calc(50% + 45px);
  }
  .result .swiper-button-next {
    left: calc(50% + 115px);
  }
  .footer__list {
    padding: 15px 50px;
  }
  .footer__list a {
    font-size: 18px;
  }
}
@media (max-width: 1200px) {
  .service__wrapper .swiper-wrapper {
    gap: 20px;
  }
  .footer__list {
    padding: 15px 20px;
  }
  .footer__wrapper {
    gap: 20px;
  }
}
@media (max-width: 1024px) {
  section {
    padding-bottom: 40px;
  }
  .title {
    margin: 0 -15px;
    padding: 0 15px 14px;
    font-size: 20px;
  }
  .nav {
    padding: 20px 15px 0;
    position: relative;
    display: block;
  }
  .nav__phone, .nav__logo, .nav__enter, .nav__list, .nav__row {
    display: none;
  }
  .nav__mob {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
  .nav__mob_row {
    border-radius: 100px;
    background: #f7f7f7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
  }
  .nav__mob_logo {
    display: none;
  }
  .nav__humburger {
    width: 20px;
    height: 16px;
    position: relative;
    cursor: pointer;
  }
  .nav__humburger span, .nav__humburger::before, .nav__humburger::after {
    content: "";
    width: 100%;
    height: 1px;
    background: #5fb528;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
  .nav__humburger span {
    top: 50%;
  }
  .nav__humburger::after {
    top: 100%;
  }
  .nav.active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #f7f7f7;
    z-index: 3;
    padding: 25px 20px;
  }
  .nav.active .nav__mob {
    display: none;
  }
  .nav.active .nav__mob_row {
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: start;
  }
  .nav.active .nav__mob_logo {
    display: block;
    max-width: 150px;
  }
  .nav.active .nav__mob_logo img {
    width: 100%;
  }
  .nav.active .nav__humburger span {
    opacity: 0;
  }
  .nav.active .nav__humburger::before, .nav.active .nav__humburger::after {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .nav.active .nav__humburger::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .nav.active .nav__row {
    display: block;
    margin: 44px 0 0;
    padding: 0;
  }
  .nav.active .nav__row ul {
    display: grid;
    grid-template-columns: 1fr;
    gap: 28px;
  }
  .nav.active .nav__row ul li {
    padding: 0 0 28px;
    border-bottom: 1px solid #c3e49a;
  }
  .nav.active .nav__row ul a {
    font: 400 20px "AA Stetica", sans-serif;
    color: #32302f;
  }
