  .acc__cours_desc h3 {
    font-size: 20px;
  }
  .acc__cours_desc p {
    font-size: 16px;
    margin: 11px 0 0;
  }
  .acc__cours_desc p + p {
    margin: 20px 0 0;
    font-size: 16px;
  }
  .acc__cours_info p {
    text-align: left;
    font-size: 16px;
  }
  .acc__cours_info p br {
    display: none;
  }
  .acc__main {
    padding: 0;
    background: transparent;
  }
  .acc__main_prev {
    max-width: 136px;
  }
  .acc__main_prev img {
    height: 136px;
    object-fit: cover;
  }
  .acc__main_prev a {
    display: none;
  }
  .acc__main_prev h3 {
    margin: 20px 0 0;
    font-size: 24px;
  }
  .acc__main_prev p {
    margin: 20px 0 0;
    font-size: 20px;
  }
  .acc__arrow {
    display: none;
  }
}
@media (max-width: 500px) {
  .event__slide_acc .event__desc {
    width: calc(100% - 130px);
  }
  .event__slide_acc .event__date {
    margin: 0 0 0 auto;
  }
  .event__slide_acc .btn__trans {
    margin-left: auto;
  }
  .acc__cours_info .btn__trans {
    width: 100%;
  }
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 15px;
  z-index: 2;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.modal.active {
  opacity: 1;
  pointer-events: all;
}
.modal__overflow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  z-index: -1;
}
.modal__block {
  border-radius: 20px;
  background: #fff;
  width: 100%;
  max-width: 622px;
  padding: 98px 45px;
  margin: auto;
  position: relative;
}
.modal__block h2 {
  font: 700 36px/1.5 "AA Stetica", sans-serif;
  color: #32302f;
  padding: 0 0 20px;
  border-bottom: 1px solid #c3e49a;
}
.modal__close {
  position: absolute;
  top: 50px;
  right: 44px;
  cursor: pointer;
}
.modal__back {
  position: absolute;
  top: 50px;
  left: 44px;
  cursor: pointer;
}
.modal__input {
  margin: 40px 0 0;
  display: grid;
  gap: 20px;
}
.modal__input input {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #c3e49a;
  border-radius: 80px;
  padding: 13px 20px;
  font: 400 20px/1.5 "AA Stetica", sans-serif;
  color: #575352;
  background: transparent;
  transition: all 0.3s;
}
.modal__input input::placeholder {
  color: #d9d9d9;
}
.modal__input input:hover {
  background: #f6fbe7;
}
.modal__input input:hover::placeholder {
  color: #c3e49a;
}
.modal__input input:focus::placeholder {
  color: #c3e49a;
}
.modal__check {
  margin: 20px 0 0;
  display: grid;
  grid-template-columns: 23px 1fr;
  gap: 9px;
  align-items: center;
  position: relative;
}
.modal__check p {
  font: 400 16px/1.5 "Inter", sans-serif;
  color: #5fb528;
}
.modal__check span {
  width: 23px;
  height: 23px;
  border: 1px solid #c3e49a;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}
.modal__check span svg {
  opacity: 0;
  transition: all 0.3s;
}
.modal__check input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.modal__check input:checked + span {
  background: #5fb528;
  border: 1px solid #5fb528;
}
.modal__check input:checked + span svg {
  opacity: 1;
}
.modal__btn {
  margin: 40px 0 0;
  display: grid;
  gap: 20px;
}
.modal__btn .btn__green,
.modal__btn .btn__trans {
  padding: 15px 19px;
  font-size: 20px;
}
.modal__text {
  margin: 23px 0 0;
  font: 400 14px/1.5 "AA Stetica", sans-serif;
  color: #5fb528;
}
.modal__link {
  display: block;
  margin: 75px 0 0;
  font: 400 20px/1.5 "AA Stetica", sans-serif;
  color: #5fb528;
  text-align: center;
}
.modal__link span {
  text-decoration: underline;
  text-decoration-skip-ink: none;
}

@media (max-width: 1024px) {
  .modal {
    padding: 0;
  }
  .modal__block {
    width: 100%;
    height: 100%;
    max-width: 100%;
    border-radius: 0;
    padding: 75px 15px;
  }
  .modal__block h2 {
    font-size: 24px;
    padding: 0 0 12px;
  }
  .modal__close {
    top: 30px;
    right: 15px;
  }
  .modal__back {
    top: 30px;
    left: 15px;
  }
  .modal__check p {
    font-size: 12px;
  }
  .modal__link {
    margin: 12px 0 0;
    font-size: 16px;
  }
}